import React, { useState } from 'react';
import {
  SingleFeedReplyLayout,
  ReplyBox,
  ReplyUserProfile,
  ReplyContent,
} from './SingleReedReplyStyle';

function SingleFeedReplies() {
  const [input, setInput] = useState(false);

  const showInput = () => {
    setInput(!input);
  };
  return (
    <SingleFeedReplyLayout>
      <ReplyBox>
        <ReplyUserProfile
          src="https://media.bunjang.co.kr/product/166788639_1_1634025108_w360.jpg"
          alt="user profile"
        />
        <ReplyContent>
          <p>
            <span>userId</span>
            Authorization      
                     
            .        .{' '}
          </p>

          {!input ? (
            <button type="button" className="reply-button" onClick={showInput}>
              
            </button>
          ) : (
            <div style={{ display: 'flex' }}>
              <button
                type="button"
                className="reply-button"
                onClick={showInput}
              >
                
              </button>
              <button
                type="button"
                className="reply-cancel"
                onClick={showInput}
              >
                
              </button>
            </div>
          )}

          {input ? (
            <input
              type="text"
              className="reply-input"
              placeholder="add reply"
            />
          ) : (
            ''
          )}
        </ReplyContent>
      </ReplyBox>
    </SingleFeedReplyLayout>
  );
}

export default SingleFeedReplies;
